import { Typography, Box } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import classNames from "classnames";
import React from "react";

type DividerColor = "divider" | "primary" | "secondary";

export default function SectionDivider(props: {
  className?: string;
  label?: string;
  color?: DividerColor;
  variant?:
    | "button"
    | "caption"
    | "h1"
    | "h2"
    | "h3"
    | "h4"
    | "h5"
    | "h6"
    | "inherit"
    | "subtitle1"
    | "subtitle2"
    | "body1"
    | "body2"
    | "overline"
    | "srOnly";
  fontWeight?:
    | "fontWeightLight"
    | "fontWeightRegular"
    | "fontWeightMedium"
    | "fontWeightBold"
    | number;
}) {
  const {
    className = "",
    label,
    color = "divider",
    variant,
    fontWeight = "fontWeightBold",
  } = props;
  const classes = useStyles({ color });
  return (
    <div className={classNames(classes.root, className)}>
      <div className={classes.dividerLeft} />
      <Typography component="div" variant={variant} className={classes.label}>
        <Box fontWeight={fontWeight}>{label}</Box>
      </Typography>
      <div className={classes.dividerRight} />
    </div>
  );
}

const useStyles = makeStyles((theme) => {
  const { typography, breakpoints, palette, spacing, transitions } = theme;
  const getColor = (color: DividerColor) =>
    color === "divider"
      ? palette.divider
      : color === "primary"
      ? palette.primary.light
      : palette.secondary.light;

  const divider = {
    display: "block",
    height: "0.6em",
    flexGrow: 1,
  };
  return {
    root: {
      width: "100%",
      display: "flex",
      flexDirection: "row",
      justifyContent: "space-between",
      alignItems: "center",
      marginBottom: theme.spacing(0.5),
    },
    label: {
      padding: spacing(0, 1),
    },
    dividerLeft: ({ color }: { color: DividerColor }) => ({
      ...divider,
      background: getColor(color),
      color: palette.getContrastText(getColor(color)),
      borderRadius: "0.5em 0 0 0.5em",
    }),
    dividerRight: ({ color }: { color: DividerColor }) => ({
      ...divider,
      background: getColor(color),
      color: palette.getContrastText(getColor(color)),
      borderRadius: "0 0.5em 0.5em 0",
    }),
  };
});
