import {
  Button,
  Container,
  Paper,
  Theme,
  Tooltip,
  Typography,
} from "@material-ui/core";
import GitHubIcon from "@material-ui/icons/GitHub";
import MoreHorizIcon from "@material-ui/icons/MoreHoriz";
import VisibilityIcon from "@material-ui/icons/Visibility";
import { makeStyles } from "@material-ui/styles";
import { graphql, useStaticQuery } from "gatsby";
import { FluidObject } from "gatsby-image";
import React from "react";

import TechnologyInfo from "../utils/technology-info";
import Image from "./image";
import SectionDivider from "./section-divider";
import TechnologyTag from "./technology-tag";

type LinkInfo = {
  label: string;
  url?: string;
  icon: React.ReactNode;
};

export default function Projects() {
  const classes = useStyles();
  const query = useStaticQuery(graphql`
    query {
      allFile(
        filter: { relativePath: { regex: "projects/mythicalmisfits/" } }
      ) {
        nodes {
          childImageSharp {
            fluid {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    }
  `);
  const title = "Personal Projects";
  const projects: {
    key: string;
    title: string;
    description: string;
    notes?: string;
    links: {
      sourceCode: LinkInfo;
      livePreview: LinkInfo;
      readMore: LinkInfo;
    };
    technologies: {
      [type in "frontend" | "backend"]: {
        label: string;
        techInfo: typeof TechnologyInfo.React;
      }[];
    };
    previewImages: FluidObject[];
  }[] = [
    {
      key: "mythicalmisfits",
      title: "Mythical Misfits",
      links: {
        sourceCode: { label: "Source Code", icon: <GitHubIcon /> },
        livePreview: {
          label: "Live Preview",
          url: "mythicalmisfits.io",
          icon: <VisibilityIcon />,
        },
        readMore: { label: "Read More", icon: <MoreHorizIcon /> },
      },
      description: `This is a responsive single page web application for multiplayer indie game that I'm developing on my free time. The main idea was to create a menu system using modern frontend web frameworks and the actual game would run in the window through webGL.`,
      notes: `*Note: Mobile responsiveness is still experimental. Mainly just issues with scrolling, but overall it scales and works pretty well.`,
      technologies: {
        frontend: [
          { label: "ReactJS", techInfo: TechnologyInfo.React },
          {
            label: "PixiJS (WebGL Game Engine)",
            techInfo: TechnologyInfo.Pixijs,
          },
          { label: "HTML5", techInfo: TechnologyInfo.Html5 },
          { label: "CSS3", techInfo: TechnologyInfo.Css3 },
          { label: "Sass", techInfo: TechnologyInfo.Sass },
          { label: "Webpack", techInfo: TechnologyInfo.Webpack },
        ],
        backend: [
          { label: "C# / .Net", techInfo: TechnologyInfo.CSharp },
          { label: "AWS Amplify", techInfo: TechnologyInfo.Amplify },
          {
            label: "AWS API Gateway",
            techInfo: TechnologyInfo.ApiGateway,
          },
          { label: "AWS Cognito", techInfo: TechnologyInfo.Cognito },
          { label: "AWS Lambda", techInfo: TechnologyInfo.Lambda },
          { label: "AWS Route 53", techInfo: TechnologyInfo.Route53 },
          { label: "AWS S3", techInfo: TechnologyInfo.S3 },
        ],
      },
      previewImages: query.allFile.nodes.map((node: any) => {
        return node.childImageSharp.fluid;
      }) as FluidObject[],
    },
  ];
  return (
    <div id="projects" className={classes.wrapper}>
      <Container>
        <Typography
          variant="h3"
          align="center"
          color="textPrimary"
          gutterBottom
        >
          {title}
        </Typography>
        <Paper className={classes.content}>
          {projects.map((project) => {
            return (
              <div key={project.title}>
                <SectionDivider
                  variant="h4"
                  label={project.title}
                  fontWeight="fontWeightRegular"
                />
                <div className={classes.projectBox}>
                  {project.previewImages.map((image, idx) => {
                    return (
                      <Image key={`${project.key}-${idx}`} fluid={image} />
                    );
                  })}
                  <div>
                    <SectionDivider label="Description" />
                    <Typography
                      variant="h6"
                      color="textSecondary"
                      className={classes.projectDescription}
                    >
                      {project.description}
                      <br />
                      <div className={classes.notes}>{project.notes}</div>
                    </Typography>
                  </div>
                  <div className={classes.technologiesBox}>
                    <div className={classes.technologies}>
                      <SectionDivider label="Frontend" />
                      <div className={classes.technologyChips}>
                        {project.technologies.frontend.map((tech) => {
                          return (
                            <TechnologyTag
                              key={tech.label}
                              icon={tech.techInfo.icon}
                              label={tech.label}
                            />
                          );
                        })}
                      </div>
                    </div>
                    <div className={classes.technologies}>
                      <SectionDivider label="Backend" />
                      <div className={classes.technologyChips}>
                        {project.technologies.backend.map((tech) => {
                          return (
                            <TechnologyTag
                              key={tech.label}
                              icon={tech.techInfo.icon}
                              label={tech.label}
                            />
                          );
                        })}
                      </div>
                    </div>
                    <div className={classes.technologies}>
                      <SectionDivider label="Links" />
                      <div className={classes.links}>
                        {Object.keys(project.links).map((key) => {
                          return (
                            <Tooltip
                              key={key}
                              title={
                                project.links[key].url ? "" : "Not Available"
                              }
                              aria-label={
                                project.links[key].url ? "" : "Not Available"
                              }
                            >
                              <span>
                                <Button
                                  key="sourcecode"
                                  aria-label="source code button"
                                  variant="contained"
                                  color="primary"
                                  href={project.links[key].url}
                                  size="large"
                                  endIcon={project.links[key].icon}
                                  disabled={!project.links[key].url}
                                >
                                  {project.links[key].label}
                                </Button>
                              </span>
                            </Tooltip>
                          );
                        })}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            );
          })}
        </Paper>
      </Container>
    </div>
  );
}

const useStyles = makeStyles((theme: Theme) => ({
  wrapper: {
    padding: theme.spacing(6, 0, 6),
  },
  content: {
    display: "grid",
    columnGap: "1em",
    padding: "1em 2em 2em 2em",
  },
  projectTitle: {
    textAlign: "center",
  },
  projectBox: {
    display: "grid",
    gridTemplateColumns: "1fr 1fr",
    [theme.breakpoints.down("sm")]: {
      gridTemplateColumns: "1fr",
    },
    columnGap: "2em",
    rowGap: "1em",
  },
  projectDescription: {
    padding: "0 1em",
  },
  notes: {
    paddingLeft: theme.spacing(5),
    fontSize: "0.75em",
  },
  technologiesBox: {
    display: "flex",
    flexDirection: "column",
    alignItems: "flex-start",
    "& > *": {
      margin: theme.spacing(1),
    },
  },
  technologies: {
    width: "100%",
  },
  technologyChips: {
    display: "flex",
    flexWrap: "wrap",
    flexBasis: 1,
    "& > *": {
      margin: theme.spacing(0.5),
    },
  },
  links: {
    display: "flex",
    flexDirection: "row",
    justifyContent: "center",
    alignItems: "center",
    width: "100%",
    "& > *": {
      margin: theme.spacing(0, 1),
      whiteSpace: "nowrap",
    },
  },
}));
